.header {
  background-color: #faf5cc;
  color: #000000;
  text-align: left;
  padding: 10px 20px;
}




.header h1 {
  margin: 0;
  font-size: 24px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}




.lion-saying-quiz-text {
  font-family: 'Arial', sans-serif;
  font-size: 40px;
  font-weight: bold;
  color: #ffffff;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center; /* 가운데 정렬 */
  margin-top: 80px; /* 화면 아래로 20px만큼 내림 */
  margin-bottom: -120px; /* 아래쪽 여백을 10px로 설정 */
}




.box {
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
  vertical-align: top; /* 세로 정렬을 위해 상단으로 정렬 */
}




.quiz-box {
  position: relative; /* 부모 요소로 설정 */
  text-align: left;
  background-color: #ffffff;
  width: 640px;
  margin: 120px auto 20px; /* 위쪽 여백을 80px로 조정 */
  padding: 30px; /* 패딩 추가 */
  height: 200px; /* 높이 조절 */
  overflow: auto; /* 내용이 넘칠 경우 스크롤 생성 */
  font-size: 30px;
}








.answer-input-box,
.enter-box,
.score-box {
  display: inline-block; /* 가로로 정렬 */
  width: calc(100% / 3); /* 화면의 1/3 */
}




.box-wrapper {
  text-align: center; /* 가운데 정렬 */
}




.answer-input-box {
  display: inline-block; /* 가로로 정렬 */
  background-color: #f0f0f0;
  text-align: center; /* 가운데 정렬 */
  width: 340px; /* 정답 박스의 가로 크기를 150px로 지정 */
  margin-right: 10px; /* 간격 조정 */
  border: none; /* 테두리 제거 */
  font-size: 20px;
}


.enter-box {
  display: inline-block; /* 가로로 정렬 */
  background-color: #5745f5;
  color: #f0f0f0;
  text-align: center; /* 가운데 정렬 */
  width: 40px; /* ENTER 박스의 가로 크기를 100px로 지정 */
  height: 20px;
  margin-right: 10px; /* 간격 조정 */
  font-weight: bold; /* 볼드 스타일 적용 */
  line-height: 20px; /* 박스의 높이와 동일하게 설정 */
  vertical-align: middle; /* 수직 정렬 */
  padding-left: 10px; /* 왼쪽으로 이동시킬 크기 */
  cursor: pointer; /* 마우스 커서를 포인터로 변경 */
  font-size: 15px;
}




.score-box {
  display: inline-block; /* 가로로 정렬 */
  background-color: #ff5733;
  color: #f0f0f0;
  text-align: center; /* 가운데 정렬 */
  width: 180px; /* SCORE 박스의 가로 크기를 120px로 지정 */
  height: 20px;
  font-size: 30px; /* 폰트 크기 조정 */
  font-weight: bold; /* 볼드 스타일 적용 */
  line-height: 20px; /* 박스의 높이와 동일하게 설정 */
  vertical-align: middle; /* 수직 정렬 */
 }


.answer-check-box {
  background-color: #f0f0f0;
  width: calc(700px - 40px); /* 퀴즈 박스의 가로 크기에 맞게 조정 */
  height: 50px;
  text-align: center; /* 가운데 정렬 */
  margin: 0 auto; /* 가운데 정렬 */
  font-size: 30px;
}


.quiz-game-container {
  position: relative; /* 질문 박스를 기준으로 위치 설정 */
}


.end-box {
  position: absolute; /* 절대 위치로 설정 */
  background-color: #ffffff;
  top: 140px; /* 상단 정렬 */
  left: 50%; /* 가운데 정렬 */
  transform: translateX(-50%); /* 수평 가운데 정렬 */
  width: 660px; /* 박스의 가로 크기 */
  font-size: 30px;
  margin-top: -130px; /* 박스를 위쪽으로 이동하여 겹치도록 설정 */
  z-index: 2; /* 다른 요소 위에 표시되도록 설정 */
  height: 120px;
}






.start-box {
  background-color: #b1adad;
  color: rgb(255, 255, 255);
  width: 660px; /* 박스의 고정된 너비 */
  height: 20px; /* 박스의 고정된 높이 */
  text-align: center; /* 가운데 정렬 */
  margin-top: 20px; /* 위쪽 여백 20px를 유지 */
  position: absolute; /* 절대 위치 지정 */
  top: 180px;
  left: 50%; /* 가운데 정렬 */
  transform: translateX(-50%); /* 수평 정렬 */
  z-index: 4; /* 다른 박스들보다 위에 배치 */
  font-size: 40px; /* 폰트 크기를 30px로 설정 */
  display: flex;
  align-items: center; /* 수직 가운데 정렬 */
  justify-content: center; /* 수평 가운데 정렬 */
  cursor: pointer; /* 마우스 커서를 포인터로 변경 */
}




.restart-box {
  background-color: #000000;
  color: rgb(255, 0, 0);
  width: 140px; /* 박스의 고정된 너비 */
  height: 20px; /* 박스의 고정된 높이 */
  text-align: center; /* 가운데 정렬 */
  margin-top: 20px; /* 위쪽 여백 20px를 유지 */
  position: absolute; /* 절대 위치 지정 */
  top: 450px;
  left: 50%; /* 가운데 정렬 */
  transform: translateX(-50%); /* 수평 정렬 */
  z-index: 2; /* 다른 박스들보다 위에 배치 */
  font-size: 30px;
  display: flex;
  align-items: center; /* 수직 가운데 정렬 */
  justify-content: center; /* 수평 가운데 정렬 */
  cursor: pointer; /* 마우스 커서를 포인터로 변경 */
}




.countdown-box {
  background-color: #000000;
  color: rgb(255, 238, 0);
  width: 660px; /* 박스의 고정된 너비 */
  height: 20px; /* 박스의 고정된 높이 */
  text-align: center; /* 가운데 정렬 */
  line-height: 50px; /* 수직 정렬을 위한 줄 높이 */
  margin-top: 20px; /* 위쪽 여백 20px를 유지 */
  position: absolute; /* 절대 위치 지정 */
  top: 180px;
  left: 50%; /* 가운데 정렬 */
  transform: translateX(-50%); /* 수평 정렬 */
  z-index: 3; /* 다른 박스들보다 위에 배치 */
  font-size: 40px; /* 폰트 크기를 30px로 설정 */
  display: flex;
  align-items: center; /* 수직 가운데 정렬 */
  justify-content: center; /* 수평 가운데 정렬 */


}




.start-box {
  float: left;
}




.restart-box {
  float: left;
}


.countdown-box {
  float: left;
}




body {
  background-color: rgb(199, 158, 247);
}


.background-shape {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #b074d3; /* 배경색 설정 */
    z-index: -1; /* 다른 요소 위에 오지 않도록 설정 */
    clip-path: polygon(0 0, 100% 0, 100% 70%, 0% 100%); /* 원하는 모양으로 설정 */
  }


  @media (max-width: 768px) {
     /* HEADER 박스, QUIZBOX, .answer-check-box 스타일 조정 */
    .header, .quiz-box, .answer-check-box {
      width: 100%; /* 화면 너비에 맞게 조정 */
      box-sizing: border-box; /* 패딩과 보더를 포함한 너비로 설정 */
    }


    .quiz-box{
      font-size: 20px;
      height: 200px;
      padding-top: 15px;
    }


    .lion-saying-quiz-text{
      margin-top: 50px;
      margin-bottom: -130px;
    }


 
    /* .start-box, .restart-box, .countdown-box 스타일 조정 */
    .start-box, .countdown-box, .restart-box {
      width: 100%; /* 화면 너비에 맞게 조정 */
      box-sizing: border-box; /* 패딩과 보더를 포함한 너비로 설정 */
      /* 필요에 따라 마진, 패딩 조정 */
      margin-top: -20px;
    }


    .start-box {
      /* .start-box의 세로 크기 조정 */
      font-size: 30px;
      height: 50px; /* 높이를 60px로 설정, 필요에 따라 조정 */
    }
 


    .restart-box {
      font-size: 20px;
      top:47.8%;
      height: 49px; /* 높이를 40px로 설정, 필요에 따라 조정 */
      z-index: 4;
    }


    .countdown-box {
      /* .countdown-box의 세로 크기 조정 */
      font-size: 30px;
      height: 49px; /* 높이를 40px로 설정, 필요에 따라 조정 */
    }
   


 
    /* 텍스트 중앙 정렬을 위한 추가 스타일링 */
   .answer-check-box {
      text-align: center; /* 텍스트 중앙 정렬 */
      display: flex;
      justify-content: center; /* Flex 컨테이너 내에서 가로 방향 중앙 정렬 */
      align-items: center; /* Flex 컨테이너 내에서 세로 방향 중앙 정렬 */
      height: auto; /* 높이 자동 조정, 필요에 따라 고정값 지정 가능 */
    }


    .answer-check-box{
      font-size: 20px;
      font-weight: bolder;
    }
 
  }




  @media (max-width: 768px) {
    .box-wrapper {
        display: flex; /* Flex 컨테이너 설정 */
        flex-wrap: nowrap; /* 요소들이 줄바꿈되지 않도록 설정 */
    }
   
    .answer-input-box {
        width: 50%; /* 8의 비율로 너비를 설정 */
        min-width: 0; /* flex 항목이 줄어들 수 있도록 설정 */
        font-size: 15px;
        margin-right: 2px;
    }
   
    .enter-box {
        width: 10%; /* 2의 비율로 너비를 설정 */
        min-width: 0; /* flex 항목이 줄어들 수 있도록 설정 */
        font-size: 10px;
        margin-right: 2px;
    }


    /* 필요한 경우 .score-box에 대한 추가 스타일 */
    .score-box {
        width: 40%; /* 전체 너비를 차지하도록 설정 */
        margin-top: 10px; /* 위쪽 마진을 추가하여 간격 설정 */
        font-size: 15px;
    }
}
